<template>
  <div class="input-phone-container flex-start align-center">
    <el-dropdown class="el-dropdown-style" @command="onCommand">
      <span class="el-dropdown-link">
        {{ areaVal }}<i class="el-icon-arrow-down el-icon--right"></i>
      </span>
      <el-dropdown-menu slot="dropdown">
        <el-dropdown-item
          v-for="item of options"
          :key="item.value"
          :command="item.value"
        >
          {{ item.label }}
        </el-dropdown-item>
      </el-dropdown-menu>
    </el-dropdown>
    <el-input
      class="el-input-style"
      :value="inputVal"
      placeholder="请输入手机号"
      @input="onInput"
    ></el-input>
  </div>
</template>

<script>
export default {
  props: {
    // 输入的手机号
    inputVal: {
      type: String,
      default: "",
      required: true,
    },
  },
  data() {
    return {
      // 区号选项列表
      options: [
        {
          value: "+86",
          label: "+86",
        },
      ],
      // 选中的区号
      areaVal: "+86",
    };
  },
  methods: {
    /**
     * 选择区号
     */
    onCommand(val) {
      console.log("val :>> ", val);
      this.areaVal = val;
      this.$emit("command", val);
    },
    /**
     * 输入手机号
     */
    onInput(val) {
      this.$emit("update:inputVal", val);
    },
  },
};
</script>

<style lang="scss" scoped>
.input-phone-container {
  padding: px(0) px(12);
  background: #ffffff;
  border-radius: px(8);
  border: px(1) solid #eeeeee;

  .el-dropdown-style {
    width: px(60);
    .el-dropdown-link {
      cursor: pointer;
      font-size: px(14);
      font-weight: bold;
      color: #333333;
    }
    .el-icon-arrow-down {
      font-size: 12px;
    }
  }

  .el-input-style {
    ::v-deep {
      .el-input__inner {
        border: none;
      }
    }
  }
}

.el-dropdown-menu__item {
  padding: 0 px(12);
}
</style>
